import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import List from './views/List.vue'
import Note from './views/Note.vue'
import HomePage from './views/HomePage.vue'



Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      children: [
        {
          path: 'HomePage',
          name: 'HomePage',
          component: HomePage,
        },
        {
          path: 'List',
          name: 'List',
          component: List,
        },
        {
          path: 'Note',
          name: 'Note',
          component: Note,
        },
      ]
    },
  ]
})
